<template>
  <div class="board-detail">
    <div class="common-buttons">
      <button type="button" class="w3-button w3-round w3-blue-gray" @click="fnUpdate">수정</button>&nbsp;
      <button type="button" class="w3-button w3-round w3-red" @click="fnDelete">삭제</button>&nbsp;
      <button type="button" class="w3-button w3-round w3-gray" @click="fnList">목록</button>&nbsp;
    </div>
    <div class="board-contents">
      <h3>{{ title }}</h3>
      <div>
        <strong class="w3-large">{{ name }}</strong>
        <br>
        <span>{{ writeDay }}</span>
      </div>
    </div>
    <div class="board-contents">
      <span>{{ contents }}</span>
    </div>
    <div class="common-buttons">
      <button type="button" class="w3-button w3-round w3-blue-gray" v-on:click="fnUpdate">수정</button>&nbsp;
      <button type="button" class="w3-button w3-round w3-red" v-on:click="fnDelete">삭제</button>&nbsp;
      <button type="button" class="w3-button w3-round w3-gray" v-on:click="fnList">목록</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      requestBody : this.$route.query,
      id: this.$route.query.id,
      title : '',
      name : '',
      contents : '',
      write_daty : '',
    }
  },
  mounted() {
    this.getView()
  },
  methods: {
    getView() {
      this.$axios.get(this.$serverUrl + '/board/detail/' + this.id, {}
      ).then((res) => {
        this.title = res.data.title
        this.name = res.data.name
        this.contents = res.data.contents
        this.write_daty = res.data.writeDay
      }).catch((err) => {
        if (err.message.indexOf('Network Error') > -1) {
          alert('네트워크가 원활하지 않습니다.\n잠시 후 다시 시도해주세요.')
        }
      })
    },
    fnList() {
      delete this.requestBody.id
      this.$router.push({
        path: './list',
        query: this.requestBody
      })
    },
    fnUpdate() {
      this.$router.push({
        path: './write',
        query: this.requestBody
      })
    },
    fnDelete() {
      if (!confirm("삭제하시겠습니까?")) return

      this.$axios.delete(this.$serverUrl + '/board/' + this.id, {})
      .then(() => {
        alert('삭제되었습니다.')
        this.fnList();
      }).catch((err) => {
        console.log(err);
      })
    }
  }
}
</script>