<template>
  <div class="board-list">
    <div class="common-buttons">
      <buoon type="button" class="w3-button w3-round w3-blue-gray" v-on:click="write">등록</buoon>
    </div>
    <table class="w3-table-all">
      <thead>
        <tr>
          <th>No</th>
          <th>제목</th>
          <th>작성자</th>
          <th>등록일시</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(row, id) in list" :key="id">
          <td>{{row.id}}</td>
          <td><a v-on:click="view(`${row.id}`)">{{row.title}}</a></td>
          <td>{{row.name}}</td>
          <td>{{row.writeDay}}</td>
        </tr>
      </tbody>
    </table>
    <div class="pagination w3-bar w3-padding-16 w3-small" v-if="paging.totalListCnt > 0">
      <span class="pg">
        <a href="javascript:;" @click="fnPage(1)" class="first w3-button w3-border">&lt;&lt;</a>
        <a href="javascript:;" v-if="paging.startPage > 10" @click="fnPage(`${paging.startPage-1}`)" class="prev w3-button w3-border">&lt;</a>
        <template v-for="(n, index) in paginavigation()">
          <template v-if="paging.page === n">
            <strong class="w3-button w3-border w3-green" :key="index">{{ n }}</strong>
          </template>
          <template v-else>
            <a class="w3-button w3-border" href="javascript:;" @click="fnPage(`${n}`)" :key="index">{{ n }}</a>
          </template>
        </template>
        <a href="javascript:;" v-if="paging.totalPageCnt > paging.endPage" @click="fnPage(`${paging.endPage+1}`)" class="next w3-button w3-border">&gt;</a>
        <a href="javascript:;" @click="fnPage(`${paging.totalPageCnt}`)" class="last w3-button w3-border">&gt;&gt;</a>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      requestBody: {},
      list: {},
      no: '',
      paging: {
        block: 0,
        endPage: 0,
        next_block: 0,
        page: 0,
        pageSize: 0,
        prevBlock: 0,
        startIndex: 0,
        startPage: 0,
        totalBlockCnt: 0,
        totalListCnt: 0,
        totalPageCnt: 0,
      },
      page: this.$route.query.page ? this.$route.query.page : 1,
      size: this.$route.query.size ? this.$route.query.size : 10,
      keyword: this.$route.query.keyword,
      paginavigation: function() {
        let pageNumber = [] //;
        let start_page = this.paging.startPage;
        let end_page = this.paging.endPage;
        for (let i = start_page; i<= end_page; i++) pageNumber.push(i);
        return pageNumber;
      }
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    getList() {
      this.requestBody = { // 데이터전송
        keyword: this.keyword,
        page: this.page,
        size: this.size
      }

      this.$axios.get(this.$serverUrl + "/board/list", {
        params: this.requestBody,
        headers: {}
      }).then((res) => {
        if (res.data.resultCode === "OK") {
          this.list = res.data.data
          this.paging = res.data.pagination
          this.no = this.paging.totalListCnt - ((this.paging.page - 1) * this.paging.pageSize)

          console.log(this.paging);
          console.log(this.no);
        }
      }). catch((err) => {
        if (err.message.indexOf('Network Error') > -1 ) {
          alert('네트워크가 원활하지 않습니다. \n잠시 후 다시 시도해주세요.')
        }
      })
    },
    view(id) {
      this.requestBody.id = id;
      this.$router.push({
        path: './detail',
        query: this.requestBody
      })
    },
    write() {
      this.$router.push({
        path: './write'
      })
    },
    fnPage(n) {
      if (this.page !== n) {
        this.page = n
        this.getList()
      }
    }
  }
}
</script>